class Phone extends Component {
    constructor(props) {
        super(props)
        this.ticketRules = [];
        for (var i = 1; i <=8; i++) {
            this.ticketRules.push({
                text: 'buy_tickets_end' + i
            })
        }
    }

    render() {
        return <div style={{width: '100%'}}>
            <Header/>
            <div style={{padding: "0 .15rem .4rem .15rem", backgroundColor: "#fff"}}>
                <div style={{
                    height: '1.94rem',
                    width: '100%',
                    padding: '.75rem 0',
                    textAlign: 'center',
                    fontWeight: '600',
                    fontSize: "20px",
                }}>
                    <FormattedMessage id="please_select_your_ticket"/>
                </div>
                <WapTicketsArea/>
                <div style={{textAlign: "center", margin: "20px 0 20px 20px", fontSize: "20px", color: '#01007a'}}>
                    <FormattedMessage id="buy_tickets_note"/>
                </div>
                <div style={{padding: "0 15px", fontSize: '14px'}}>
                    {
                        this.ticketRules.map((item, key) => {
                            return (
                                <div key={key} style={{textAlign: "left", padding: "5px 10px", display: 'flex'}}>
                                    {
                                        (key != 7 )?<span style={{color: "#0086ff", paddingRight: '.1rem'}}>*</span> :<span style={{paddingRight:'.1rem'}}> </span>
                                    }
                                    {<FormattedMessage id={item.text}/>}
                                </div>
                            )
                        })
                    }
                    <div style={{textAlign: "left", padding: "5px 10px", display: "flex"}}>
                        <span style={{color: "red", paddingRight: '.1rem'}}>*</span>
                        {<FormattedMessage id="buy_tickets_end"/>}
                    </div>
                    <div style={{textAlign: "left", padding: "5px 10px", display: "flex"}}>
                        <span style={{color: "red", paddingRight: '.1rem'}}>*</span>
                        <div>
                            {<FormattedMessage id="after_refund_info"/>}<a  href="mailto:create_help@baidu.com"><FormattedMessage id='customer_service_mailbox'/></a>
                        </div>
                    </div>
                </div>
            </div>
            <Footer/>
        </div>
    }
}

