class PCBody extends Component {
    constructor(props) {
        super(props);
        this.ticketRules = [];
        for (var i = 1; i <= 8; i++) {
            this.ticketRules.push({
                text: 'buy_tickets_end' + i
            })
        }
    }

    componentDidMount() {
    }

    render() {
        return <div style={{
            margin: "0 auto",
            display: "block",
            position: "relative",
            width: "1200px",
            minHeight: "800px",
            background: '#fff',
            overflow: 'hidden'

        }}>
            <PCTitle>
                <FormattedMessage id="please_select_your_ticket"/>
            </PCTitle>
            <PCTicketsArea/>

            <div style={{textAlign: "center", margin: "40px 0", fontSize: "25px", color: ' rgb(11, 146, 254)'}}>
                <FormattedMessage id="buy_tickets_note"/>
            </div>
            <div>
                {
                    this.ticketRules.map((item, key) => {
                        return (

                            <div key={key} style={{textAlign: "left", margin: "20px 0"}}>
                                {
                                    (key != 7)?
                                    <span style={{color: "#0086ff", marginRight: "10px"}}>*</span>:<span style={{marginRight:'10px'}}> </span>
                                }
                                <FormattedMessage id={item.text}/>
                            </div>
                        )
                    })
                }
            </div>
            <div style={{textAlign: "left", margin: "20px 0px"}}>
                <span style={{color: "red", marginRight: "10px"}}>*</span>
                <FormattedMessage id="buy_tickets_end"/>
            </div>
            <div style={{textAlign: "left", margin: "20px 0px"}}>
                <span style={{color: "red", marginRight: "10px"}}>*</span>
                {<FormattedMessage id="after_refund_info"/>}<a  href="mailto:create_help@baidu.com"><FormattedMessage id='customer_service_mailbox'/></a>
            </div>
            <Menu ref="Menu" text="sure"/>
        </div>
    }
}

class PC extends Component {
    render() {
        return <div>
            <Header/>
            <PCBody/>
            <Footer/>
        </div>
    }
}

export class TicketTypes extends Component {
    constructor(props) {
        super(props);
    }

    changeLanguage = () => {
        if (localStorage['language'] == 'en') {
            localStorage['language'] = 'zh-CN';
        }
        else {
            localStorage['language'] = 'en';
        }
        location.href = location.href;
    };
    getLoginStatus = () => {
        let logined = false;
        $.get(Constant.url + 'hasLoginBaidu.do', (data) => {
            if (data.error_no == '0') {
                logined = false;
            }
            else {
                logined = true;
            }
        })
    }

    componentDidMount() {
        this.getLoginStatus();
    }

    login = () => {
        IsPcOrNot.get() ? window.instance.show() : window.instance.render($("#login")[0]);
    };

    render() {
        return <div style={{backgroundColor: '#ffffff'}}>
            {
                IsPcOrNot.get() ?
                    <PC/> : <Phone/>
            }
        </div>
    }
}

export default TicketTypes;



